.castList {
    list-style: none;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
}
 
.castImg {
    margin-bottom: 25px;
    height: 240px;
    width: 240px;
    object-fit: cover;
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.castImg:hover,
.castImg:focus {
    transform: scale(1.08);
    cursor: zoom-in;
}

.message {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}